body {
  margin: 0 auto;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* nav////////////////////////////////////////////////////////////// */

#bootstrap-override .navbar-light .navbar-nav .nav-link {
    color: #555555;
}

#bootstrap-override .navbar-light .navbar-nav:hover .nav-link:hover {
    font-weight: bold;
}

#bootstrap-override .btn-outline-primary {
    border: none;
    color: #555555;
}

#bootstrap-override .btn-outline-primary {
    border: none;
}

#bootstrap-override .nav-link {
    color: #555555;
    text-decoration: none;
    background-color: transparent;
}
#bootstrap-override .nav-link:hover {
    font-weight: bold;
}

#bootstrap-override a {
  color: #555555;
  text-decoration: none;
  background-color: transparent;
}
#bootstrap-override a:hover {
  font-weight: bold;
}

#dropdown-item-button {
  color: #555555;
}

#bootstrap-override .btn-outline-primary:hover {
  color: #555555;
  background-color: transparent;
  border-color: transparent;
}

#bootstrap-override .btn-outline-primary.dropdown-toggle {
    background-color: transparent;
    border-color: transparent;
}

#bootstrap-override .btn-outline-primary:hover .dropdown-toggle:hover {
    background-color: transparent;
    border-color: transparent;
}

#bootstrap-override .btn-outline-primary:not(:disabled):not(.disabled).active, .btn-outline-primary:not(:disabled):not(.disabled):active, .show>.btn-outline-primary.dropdown-toggle {
    color: #555555;
    background-color: transparent;
    border-color: transparent;
}

nav {
  padding: 20px 20px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

/* ////// MENU ///////////////////////////////////////////////////////////////////// */


.main {
  width: 960px;
  display: flex;
  justify-content: space-between;
  padding: 30px;
  flex-direction: row;
}

.menu {
  max-width: 600px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;

}

.menu h1 {
  text-align: center;
}

.menu button {
  font-size: 15px;
  border-radius: 5px;
}

label {
  margin-right: 20px;
}

.orderList {
  min-width: 300px;
  position: fixed;
  padding: 5px;
  top: 120px;
  right: 0px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid #ccc;
  border-radius: 15px;
}

.orderList h3 {
  text-align: center;
}

.title {
  display: flex;
  justify-content: flex-end;
  text-align: right;
}

.item {
  display: flex;
  justify-content: flex-start space-between;
  align-items: center;
}

.itemName {
  width: 150px;
  text-align: left;
}

.quantity {
  width: 90px;
  text-align: center;
}

.itemPrice {
  width: 60px;
  text-align: right;
  margin-right: 0;
}

.quantity button {
  margin: 5px;
}


.totalPrice {
  text-align: center;
}

.checkout-button {
  width: 150px;
  font-size: 20px;
  border-radius: 5px;
}

.checkout-button a {
  text-decoration: none;
}

.checkout-button a:hover {
  text-decoration: none;
  color: #071fba;
}


.kind-order {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}

.kind-order button {
  display: inline;
  margin-left: 20px;
  height: 40px;
  font-size: 20px;
  border-radius: 10px;
}

.payment-form {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}

.payment-form h2 {
  margin-right: 20px;
}

.payment-form label {
  font-size: 20px;
}

.ccForm {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  border: 1px solid #ccc;
  border-radius: 15px;
  padding: 5px;
}

.pay {
  width: 70px;
  height: 40px;
  font-size: 20px;
  border-radius: 10px;

}
